.social {
   display: flex;
   justify-content: space-between;
   width: 13rem;

   @include braikpoint (970px) {
      display: none;
   }
}

.social__link:hover svg path {
   fill: $color-hover;
}

.social__link:active svg path,
.social__link:focus svg path {
   fill: $color-active;
}